<template>
  <div class="actionSheet">
    <transition name="delay">
      <div class="mask" v-show="globalActionSheet && actionSheetID===id" @click="sheetHandle(false, false)"></div>
    </transition>
    <transition name="fade">
      <div class="sheet-container" v-show="globalActionSheet && actionSheetID===id">
        <div class="title bomPixel" @click="sheetHandle(false, false)">{{title}}</div>
        <div class="item bomPixel" v-for="(item, i) in list" :key="i" @click="sheetHandle(true, i+1)">
          <a :style="{'color': color}">{{item}}</a>
        </div>
        <div class="cancel" @click="sheetHandle(false, true)">
          <a>{{cancel}}</a>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  name: 'actionSheet',
  props: {
    actionSheet: {
      type: Boolean,
      default: false
    },
    index: {
      type: Number,
      default: 1
    },
    id: {
      type: Number,
      default: 0
    },
    title: {
      type: String,
      default: ''
    },
    list: {
      type: Array,
      default: () => {
        return []
      }
    },
    cancel: {
      type: String,
      required: true
    },
    color: {
      type: String,
      default: ''
    }
  },
  data () {
    return {}
  },
  computed: {
    ...mapGetters([
      'globalActionSheet', 'actionSheetID'
    ])
  },
  methods: {
    sheetHandle (confirm, value) {
      this.$emit('sheetHandle', confirm, value, this.id)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.actionSheet{
  .mask{
    .fs;
    background: rgba(0, 0, 0, .5);
  }
  .sheet-container{
    .wh(690px, auto);
    border-radius: 20px;
    background: #FFF;
    position: absolute;
    left: 50%;
    bottom: 46px;
    margin-left: -345px;
    overflow: hidden;
    .title{
      .wh-l(100%, 90px);
      font-size: 26px;
      position: relative;
      color: #AAAAAA;
    }
    .item{
      .wh-l(100%, 105px);
      font-size: 30px;
      position: relative;
      a{
        .wh(100%, 100%);
        display: block;
        color: #AAAAAA;
      }
    }
    .cancel{
      .wh-l(100%, 100px);
      font-size: 30px;
      a{
        .wh(100%, 100%);
        display: block;
        color: #AAAAAA;
      }
    }
  }
}

.delay-leave-active{
  transition-delay: .2s;
}
.fade-enter-active,.fade-leave-active{
  transition: all .15s ease-out;
}
.fade-enter, .fade-leave-to{
  transform: scale3d(.8,.8,.1);
  opacity: 0;
}
</style>



