<template>
  <div class="header bomPixel">
    <div class="statusBar"></div>
    <div class="head-left" v-if="menuWidth.left>0" :style="{width: menuWidth.left + '%'}">
      <slot name="setLeft"></slot>
      <div class="back" v-if="goBack&&!customBack" @click="$router.back()">
        <img src="../../assets/global_images/back-icon.png" />
      </div>
      <div class="back" v-if="!goBack&&customBack" @click="goBackHander">
        <img src="../../assets/global_images/back-icon.png" />
      </div>
    </div>
    <div :class="['head-center', {bold: Version === 2}]" v-if="menuWidth.title>0" :style="{width: menuWidth.title + '%'}">
      {{title}}
      <slot name="title"></slot>
    </div>
    <div class="head-right" v-if="menuWidth.right>0" :style="{width: menuWidth.right + '%'}">
      <slot name="setRight"></slot>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    title: {
      type: String,
      default: ''
    },
    goBack: {
      type: Boolean,
      default: false
    },
    customBack: {
      type: Boolean,
      default: false
    },
    proportion: {
      type: String,
      default: '25:50:25'
    }
  },

  computed: {
    menuWidth () {
      let arr = this.proportion.split(':')
      return {
        left: parseInt(arr[0]),
        title: parseInt(arr[1]),
        right: parseInt(arr[2])
      }
    }
  },

  methods: {
    goBackHander () {
      this.$emit('goBackHander')
    }
  }
}
</script>

<!-- Add  "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.header{
  .wh(100%, @statusBar + @headHeight);
  font-size: 36px;
  position: relative;
  background: @statusBarColor;
  color: #282828;
  .statusBar{
    .wh(100%, @statusBar);
    background: @statusBarColor;
  }
  .head-left{
    .wh-l(25%, @headHeight);
    float: left;
    .back{
      .wh(90px, 100%);
      img{
        height: 35px;
        vertical-align: middle;
      }
    }
  }
  .head-center{
    .wh-l(50%, @headHeight);
    float: left;
    text-align: center;
    // font-weight: 700;
  }
  .bold{
    font-weight: 700;
  }
  .head-right{
    .wh-l(25%, @headHeight);
    float: left;
  }
}

</style>



