<template>
  <div class="messageBox">
    <transition name="delay">
      <div class="mask" @click="boxHandle(false, false)" v-show="globalMessageBox && messageBoxID===id"></div>
    </transition>
    <transition name="fade">
      <div class="box-container" v-show="globalMessageBox && messageBoxID===id">
        <div class="icon">
          <img class="defic" v-if="icon==='defic' && (Version === 1 || Version === 3)" src="../../assets/global_images/defic.png" alt="">
          <img class="defic" v-if="icon==='defic' && Version === 2" src="../../assets/synerMax-skin/insufficient@3x.png" alt="">
          <img class="baby" v-if="icon==='baby'" src="../../assets/global_images/baby.png" alt="">
          <img class="offLine" v-if="icon==='offLine'" src="../../assets/global_images/off-line.png" alt="">
          <img class="listIcon" v-if="icon==='listIcon' && (Version === 1 || Version === 3)" src="../../assets/global_images/list-icon.png" alt="">
          <img class="listIcon" v-if="icon==='listIcon' && Version === 2" src="../../assets/synerMax-skin/list-icon@3x.png" alt="">
          <img class="noreturn" v-if="icon==='noreturn' && (Version === 1 || Version === 3)" src="../../assets/global_images/noreturn.png" alt="" >
          <img class="noreturn" v-if="icon==='noreturn' && Version === 2" src="../../assets/synerMax-skin/return@3x.png" alt="" >
          <img class="scan" v-if="icon==='scan' && (Version === 1 || Version === 3)" src="../../assets/global_images/scan.png" alt="">
          <img class="scan" v-if="icon==='scan' && Version === 2" src="../../assets/synerMax-skin/scan-small@3x.png" alt="" >
          <img class="battery" v-if="icon==='battery'" src="../../assets/global_images/battery-.png" alt="">
          <img class="logout" v-if="icon==='logout' && (Version === 1 || Version === 3)" src="../../assets/personal_images/logout.png" alt="">
          <img class="logout" v-if="icon==='logout' && Version === 2" src="../../assets/synerMax-skin/cncel@3x.png" alt="">
          <img class="logout" v-if="icon==='withdraw' && (Version === 1 || Version === 3)" src="../../assets/wallet_images/withdraw.png" alt="">
          <img class="logout" v-if="icon==='withdraw' && Version === 2" src="../../assets/synerMax-skin/矢量智能对象@3x.png" alt="">
          <img class="listIcon" v-if="icon==='Update' && (Version === 1 || Version === 3)" src="../../assets/sidebar_images/new.png" alt="">
          <img class="listIcon" v-if="icon==='Update' && Version === 2" src="../../assets/synerMax-skin/new@3x.png" alt="">
        </div>
        <div class="title">{{title}}</div>
        <div class="describe">{{describe}}</div>
        <div class="alert" v-if="alert!==''">
          <button @click="boxHandle(true, 1)" class="btn">{{alert}}</button>
        </div>
        <div class="confirm" v-if="confirm!==''&&cancel!==''">
          <button @click="boxHandle(false, true)">{{cancel}}</button>
          <button @click="boxHandle(true, 1)" class="btn">{{confirm}}</button>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  name: 'messageBox',
  props: {
    messageBox: {
      type: Boolean,
      default: false
    },
    id: {
      type: Number,
      default: 0
    },
    icon: {
      type: String,
      default: ''
    },
    title: {
      type: String,
      default: ''
    },
    describe: {
      type: String,
      default: ''
    },
    alert: {
      type: String,
      default: ''
    },
    confirm: {
      type: String,
      default: ''
    },
    cancel: {
      type: String,
      default: ''
    }
  },

  data () {
    return {}
  },

  computed: {
    ...mapGetters([
      'globalMessageBox', 'messageBoxID'
    ])
  },

  methods: {
    boxHandle (confirm, cancel) {
      this.$emit('boxHandle', confirm, cancel, this.id)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.messageBox{
  .mask{
    .fs;
    z-index: 998;
    background: rgba(0, 0, 0, .5);
  }
  .box-container{
    .wh(600px, 476px);
    color: #6D6D6D;
    border-radius: 20px;
    background: #FFF;
    position: absolute;
    top: 21%;
    left: 50%;
    margin-left: -300px;
    z-index: 999;
    font-size: 26px;
    .icon{
      .wh(100%, 176px);
      img{
        height: 105px;
        margin-top: 50px;
      }
      .baby{
        height: 128px;
        margin-top: 34px;
      }
      .offLine{
        height: 120px;
        margin-top: 34px;
      }
      .listIcon{
        height: 140px;
        margin-top: 34px;
      }
      .noreturn{
        height: 150px;
        margin-top: 24px;
      }
      .scan{
        height: 140px;
        margin-top: 24px;
      }
      .battery{
        height: 140px;
        margin-top: 30px;
      }
      .timeout{
        height: 146px;
        margin-top: 24px;
      }
      .logout{
        height: 120px;
      }
      .withdraw{
        height: 212px;
      }
    }
    .title{
      .wh-l(100%, 60px);
      font-size: 36px;
    }
    .describe{
      .wh(480px, 94px);
      line-height: 34px;
      margin: 0 auto;
      color: #585858;
      padding-top: 15px;
    }
    .alert{
      button{
        .wh(480px, 80px);
        background: @themeColor;
        border-radius: 40px;
        font-size: 30px;
        position: absolute;
        left: 50%;
        bottom: 35px;
        margin-left: -240px;
        box-shadow: 0px 4px 25px @themeColor;
        color: #555A5B;
      }
      .cwhite{
        color: #FFF;
      }
    }
    .confirm{
      .wh(480px, 80px);
      margin: 34px auto 0 auto;
      button:nth-child(1) {
        .wh(auto, 100%);
        float: left;
        background: #CCCCCC;
        border-radius: 40px;
        padding: 0 60px;
        color: #FFF;
        line-height: 80px;
        font-size: 30px;
      }
      button:nth-child(2) {
        .wh(auto, 100%);
        float: right;
        background: @themeColor;
        border-radius: 40px;
        padding: 0 60px;
        line-height: 80px;
        font-size: 30px;
      }
    }
  }
}

.delay-leave-active{
  transition-delay: .2s;
}
.fade-enter-active,.fade-leave-active{
  transition: all .15s ease-out;
}
.fade-enter, .fade-leave-to{
  transform: scale3d(.8,.8,.1);
  opacity: 0;
}
</style>



